@keyframes change {
    0% {
        background: red;
    }
    100% {
        background: green;
    }
}

#app4 .circle{
    border: 1px solid black;
    width:20vw;
    height:20vw;
    border-radius: 50%;
}
#app4 .circle.active{
    animation: change 1s infinite alternate linear;
}


